/**
 * General
 */
* {
	
	box-sizing: border-box;
	transition: all 0.3s ease-in-out 0s;
}

body {
	margin: 0px;
	padding: 0px;
	&.page-template-page-full {
		#main {
			max-width: 100%;
		}
	}

	h1 {
		font-size: 3.5rem;
		font-weight: 300;
		letter-spacing: -.045em;
		line-height: 3.75rem;
		margin-top: 2rem;
	}

	h2 {
		font-size: 2.75rem;
		font-weight: 600;
		letter-spacing: -0.02em;
	}

	h3 {
		font-size: 1.625rem;
		font-weight: 600;
		letter-spacing: -0.02em;
	}

	/* If WP-Adminbar is visible */
	&.admin-bar {
		#header.fixed-top {
			top: var(--wp-admin--admin-bar--height);
		}

		.edit-link a {
			color: #fff !important;
			font-weight: normal !important;
			text-decoration: none !important;

			&::before {
				content: '\270E';
				display: inline-block;
				margin-right: 5px;
			}
		}
	}
}

.blockquote {
	letter-spacing: -0.02em;
}

img {
	vertical-align: middle;
}

/**
 * WordPress customization
 */

/* WordPress Embeds */

.post-thumbnail,
.wp-video,
.embed-responsive {
	margin: 5px 0;
	width: 100% !important;
}

.post,
.post-thumbnail {
	img {
		max-width: 100%;
		height: auto;
	}
}

/* Header */

#header {
	.search-form {
		min-width: 250px;
	}

	a i {
		font-size: 36px;
		color: $primary;
	}
}

/*.navbar-brand {
	img {
		height: 40px;
	}
}*/

.navbar-static-top {
	margin-bottom: $grid-gutter-width;
}

/* Main */

/*#main {
	margin-top: $spacer;
}*/
main {
	padding-top: 100px;
}

/* Footer */

#footer {
	//background-color: #fff;
	background: #333 url("http://localhost:10004/wp-content/uploads/2024/05/bg-footer.jpg") center center;
	background-size: cover;
	color: $text-white;

	p {

		margin-top: 10px;
	}

	a {
		color: white;
	}

	.menu-item {
		a {
			&[aria-current='page'] {
				color: $link-hover-color;
			}
		}
	}
}

/* Sidebar */
#sidebar {
	h4 {
		font-size: 1.2rem;
	}

	.wp-block-latest-posts__list.wp-block-latest-posts {
		li {
			border-bottom: 1px solid;
			line-height: 1.3;
			padding: .5rem 0;
		}
	}

}

.widget-area {
	ul {
		list-style: none;
		padding: 0;
	}
}

/* Author */

.author-avatar {
	.avatar {
		width: 64px;
		height: auto;
	}
}

.author-info {
	margin: 40px 0;

	h2,
	h3 {
		margin-top: 0;
	}

/*sidebar*/

	h3 {
	background: $primary;
	color: white;
	padding: 1rem 1.5rem;
	margin: 0;
	font-size: 1.37rem;}
	.wp-block-search {
		margin-bottom: 1em;
	}
	ul li {
		padding: .75em 1.5rem;
		background-color: $light;
		border-bottom: 1px solid lighten($primary, 30%);
		//font-family: $font-family-serif;
		line-height: normal;
		font-size: 1.05em;
		&.current-menu-item, &.current-cat {
			background-color: #fff;
		}

}

}

ul.datos-puesto {
	list-style: none;
	margin: 0;
	padding: 0;
	//border-bottom: 1px solid $primary;
	li {
		border-bottom: 1px solid $primary;
		padding-top: .5rem;
		padding-bottom: .5rem;
	}
}

/* Posts */

.sticky {
	.card {
		background-color: $gray-100;
	}
}

.entry-meta {
	font-size: small;
	margin-top: ($grid-gutter-width * 0.5);
}

.single {

	.entry-content,
	.post-navigation,
	#comments-title {
		margin: $grid-gutter-width 0;
	}
}

#comments {
	margin-top: $grid-gutter-width;
}

.comment-meta {
	height: 40px;
}

.comment-reply-title {
	margin: $grid-gutter-width 0 ($grid-gutter-width * 0.5);
}

.commentlist {
	list-style: none;
	margin: 0 auto;
	width: 68.9%;

	>li.comment {
		margin: 0 0 1.625em;
		padding: 1.625em;
		position: relative;
	}

	.children {
		list-style: none;
		margin-top: $grid-gutter-width;

		>li.comment {
			border-top: 1px solid $gray-300;
			padding-top: $grid-gutter-width;
			position: relative;
		}
	}

	.avatar {
		width: 64px;
		height: 64px;
		border-radius: 50%;
		box-shadow: 0 1px 2px $gray-600;
		padding: 0;
		position: absolute;
		top: 0;
		left: -102px;
	}
}

/* WordPress Blocks */

// Buttons

.wp-block-button {
	&.is-style-outline {
		.wp-block-button__link {
			@extend .btn;
			@extend .btn-outline-primary;
		}
	}

	&.btn-lg {
		.wp-block-button__link {
			@extend .btn-lg;
		}
	}

	&.btn-sm {
		.wp-block-button__link {
			@extend .btn-sm;
		}
	}

	.wp-block-button__link {
		@extend .btn;
		@extend .btn-primary;
		text-decoration: none;
	}
}

// Table

.wp-block-table {

	td,
	th {
		border: inherit;
	}

	table {
		@extend .table;
	}
}

// Image, Media and Text

.wp-block-image,
.wp-block-media-text__media {

	img,
	video {
		height: auto;
	}
}

