		color: white;

		&:hover {
			color: white
		}
	}



	background: #000;



	ul {
		margin: 0;
		padding: 0;

		li {
			list-style: none;

			a {
				list-style: none;
				text-decoration: none;
				display: block;
				color: rgba(white, .7);
				
				

				&.active,  &:has(+ ul li a.dropdown-item.active), &.show {
					color: white !important;
					background-color: rgba(white, .3);
				}

				padding: .75rem 1.5rem;
				font-size: 1.2rem;

				&:hover {
					background-color: rgba(white, .15);
					color: rgba(white, .9) !important;
					border-color: transparent !important;
				}
			}
		}
		.dropdown-menu {
			background-color: transparent;
			.dropdown-item {
				//background-color: rgba(white, .3);
				color: rgba(white, .9) !important;
				&:hover {
					color: rgba(white, .9) !important;
					background-color: rgba(white, .12);
				}
				&.active {
					color: rgba(white, .9) !important;
					background-color: rgba(white, .20);
				}
			}
	}
	}
}

.listado-posts {
	.card {
		//height: 100%;
		height: 436px;
		margin-bottom: 1.5rem;
		border: 0;

		a {
			height: 100%;

			img {
				height: 100%;
			}
		}

		h2 {
			font-size: 1.375em;
			margin-top: 4.25em;
			//text-shadow: 0 0 30px #000;
		}

		.card-body h2 a {
			color: white;
		}

		position: relative;

		.card-body {
			padding: 0 !important;
		}

		.text-min {
			position: absolute;
			width: 100%;
			top: 0;
			left: 0;
			padding: 0 1.25em;
			color: white;
		}

		img {
			object-fit: cover;
			width: 100%;
		}


		.datos-noticia {
			position: absolute;
			top: 0;
			left: 0;
			background-color: rgba(255, 255, 255, 0.8);
			color: #000;
			font-weight: 500;
			padding: .4em 1em;
		}

		.overlay-min-blog {
			width: 100%;
			height: 100%;
			position: absolute;

			background: rgb(26, 25, 24);
			background: linear-gradient(180deg, rgba(26, 25, 24, 0.44) 0%, rgba(26, 25, 24, 0.80) 25%, rgba(26, 25, 24, 0.4) 100%);
		}

		&:hover {
			//opacity: .8;
			outline: 0;

			.text-min,
			.text-min h2 a {
				color: $primary !important;
				text-shadow: none !important;
			}

			.overlay-min-blog {
				background: transparent;
			}

			img {
				opacity: 0;
			}
		}




	}

}

.paginador {
	margin-top: 40px;
	margin-bottom: 50px;
	padding-top: 20px;
	border-top: 1px dotted $gray-300;
}

.page-numbers {
	clear: both;
	position: relative;
	background: $gray-200;
	color: #333;
	padding: 10px;
	font-size: .9rem;
	font-weight: 500;
	width: auto;

	a,
	span {
		display: block;
		float: left;
		margin: 0 1px;
		padding: 5px;
		text-decoration: none;
		text-align: center;
		width: auto;
	}

	a span {
		padding: 0;
	}

	&.current {
		background: $primary;
		color: #fff;
	}
}

a.page-numbers:hover {
	background: $gray-400;
}


